
import { defineComponent } from "vue";
import { modalController } from "@ionic/vue";
import { APM20100221_BODY, APM20100221_RES, CHILD_AUTHORIZATION_LINE, COM20700111_BODY, COM20700111_RES, PORTAL_ROLE_LIST, COMMEN_CODE_LIST } from "@/shared/type/APM20100221.type";
import { APM20100212_BODY, APM20100212_RES, PORTAL_AUTHORIZATION_LINE_SETTING_DETAIL } from "@/shared/type/APM20100212.type";
import { APM20100231_BODY, APM20100231_RES } from "@/shared/type/APM20100231.type";
import { APM20100113_BODY, APM20100113_RES, AUTHORIZATION_CODE2_LIST } from "@/shared/type/APM20100113.type";
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';

export default defineComponent({
  name: "apm201002sc01",
  components: {
    ModifyCard,
    BreadCrumb,
  },

  data() {
    const authorizationLineInfo = {
      authorizationBizTypeCode1: "",
      authorizationBizTypeCode1Name: "",
      authorizationBizTypeCode2: "",
      businessCommonStatusCode: "01",
      authorizationAuthorityTypeCode: "",
      managerApprovalDescription: "",
      list: [] as CHILD_AUTHORIZATION_LINE[]
    } as PORTAL_AUTHORIZATION_LINE_SETTING_DETAIL;

    const modifyCard = {
      registerTellerId: "",
      registerTellerName: "",
      registerDate: "",
      registerTime: "",
      updatedTellerId: "",
      updatedTellerName: "",
      updatedDate: "",
      updatedTime: "",
      deleteTellerId: "",
      deleteTellerName: "",
      deleteDate: "",
      deleteTime: "",
    };

    const mandateoryApprovalYNStatus = [
      { key: 'Y', value: 'Yes' },
      { key: 'N', value: 'No' }
    ];

    const newList = [] as CHILD_AUTHORIZATION_LINE[];
    const checkList = [] as CHILD_AUTHORIZATION_LINE[];
    const listAuthorizationBizTypeCode2 = [] as AUTHORIZATION_CODE2_LIST[];
    const listPortalRole = [] as PORTAL_ROLE_LIST[];
    const selectAuthorizationCode2 = {} as AUTHORIZATION_CODE2_LIST;
    const authCode2Edit = { authorizationBizTypeCode2: '', managerApprovalDescription: '' } as AUTHORIZATION_CODE2_LIST;
    const selectedCommonCode = { key: '', value: '' } as COMMEN_CODE_LIST;
    const selectedStatus = "";
    const selectBranchType = "01";
    const selectRole = "";
    const selectMandateoryApprovalYN = 'Y';
    const isDisableField = true;

    return {
      modifyCard,
      selectedStatus,
      isDisableField,
      selectBranchType,
      selectedCommonCode,
      selectRole,
      selectMandateoryApprovalYN,
      authorizationLineInfo,
      listAuthorizationBizTypeCode2,
      selectAuthorizationCode2,
      authCode2Edit,
      listPortalRole,
      mandateoryApprovalYNStatus,
      isInvalid: false,
      showDefault: false,
      showDisabled: false,
      newList,
      checkList,
      authCode1: '',
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode
    };
  },

  async created() {
    if (this.approvalType === '') {
      await this.inquiryPortalRoleList();

      if (this.queryRoute.type === "edit") {
        await this.authorizationLineDetail();

        this.inputUpdate();
      } else {
        this.isDisableField = false;
        this.inputRegister();
        this.authorizationLineInfo.authorizationBizTypeCode1 = ''
        this.authorizationLineInfo.authorizationBizTypeCode2 = ''
        this.selectBranchType = ''
        this.selectRole = ''
      }
    } else {
      await this.inquiryPortalRoleList();

      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        this.approvalRes = authDetail.body

        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

        const _oBody: APM20100212_BODY = {
          authorizationAuthorityTypeCode: String(authDetailParsed.authorizationAuthorityTypeCode),
          authorizationBizTypeCode1: String(authDetailParsed.authorizationBizTypeCode1),
          authorizationBizTypeCode2: String(authDetailParsed.authorizationBizTypeCode2),
        }

        const res = await this.$request.getRequest("APM20100212", _oBody, false) as APM20100212_RES;

        this.authorizationLineInfo = res.body

        if (res.header.result) {
          this.authCode1 = authDetailParsed.authorizationBizTypeCode1;
          this.authCode2Edit.authorizationBizTypeCode2 = authDetailParsed.authorizationBizTypeCode2;
          this.authCode2Edit.managerApprovalDescription = authDetailParsed.managerApprovalDescription;
          this.selectRole = authDetailParsed.list[0].authorizationRoleCode;
          this.selectedCommonCode.key = authDetailParsed.authorizationBizTypeCode1;
          this.selectedCommonCode.value = `${this.$codeConverter.convertValue('BusinessCategoryCode', authDetailParsed.authorizationBizTypeCode1)}`
        }
      })
    }
  },

  watch: {
    selectedCommonCode: {
      handler: 'getselectedCommonCode',
      deep: true
    },
    selectAuthorizationCode2: {
      handler: 'getAuthorizationCode2',
      deep: true
    },
  },

  computed: {
    queryRoute() {
      return this.$route.query;
    },
    checkSelectCommonCode(): boolean {
      return Object.keys(this.selectedCommonCode).length === 0;
    },
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  methods: {
    getselectedCommonCode(newValue: COMMEN_CODE_LIST) {
      this.authorizationLineInfo.authorizationBizTypeCode1 = newValue.key;
      if (this.queryRoute.type === 'register') {
        this.authCode1 = newValue.key;
        this.inquiryAuthorizationCode2();
      } else {
        this.authCode1 = "";
      }
    },

    getAuthorizationCode2(newValue: AUTHORIZATION_CODE2_LIST) {
      this.authorizationLineInfo.authorizationBizTypeCode2 = newValue?.authorizationBizTypeCode2;
    },

    inputRegister() {
      this.showDefault = true;
      this.showDisabled = false;
    },

    inputUpdate() {
      this.showDefault = false;
      this.showDisabled = true;
    },

    handleClickAction(actionType: string, object: CHILD_AUTHORIZATION_LINE) {
      switch (actionType) {
        case "edit":
          this.inputRegister();
          break;
        case "delete":
          this.removeOrder(object);
          break;
        case "cancel":
          this.handleCancel();
          break;
        default:
          this.goToList();
          break;
      }
    },

    handleReorder(event: CustomEvent) {
      this.authorizationLineInfo.list = event.detail.complete(
        this.authorizationLineInfo.list
      );
    },

    addOrder() {
      if (this.selectBranchType && this.selectRole && this.selectMandateoryApprovalYN) {
        if (this.authorizationLineInfo.list.length <= 4) {
          const data = this.authorizationLineInfo.list.filter((item) => item.authorizationRoleCode == this.selectRole && item.branchTypeCode == this.selectBranchType && item.mandateoryApprovalYN == this.selectMandateoryApprovalYN);

          if (data.length !== 0) {
            this.dialogMessage(this.$t('apm201002sc01.label.duplicate_message'));
          } else {
            this.authorizationLineInfo.list.push({
              branchTypeCode: this.selectBranchType,
              mandateoryApprovalYN: this.selectMandateoryApprovalYN,
              authorizationRoleCode: this.selectRole,
              seqNo: 0
            });
          }
        }
      }
    },

    removeOrder(data: CHILD_AUTHORIZATION_LINE) {
      this.$dialog.onOpen({
        content: this.$t('apm201002sc01.label.message_delete'),
        dialogMode: "Question",
        callback: () => {
          this.authorizationLineInfo.list.splice(this.authorizationLineInfo.list.indexOf(data), 1);
          modalController.dismiss();
        },
      });
    },

    handleSave() {
      this.isInvalid = false;
      this.authorizationLineInfo.authorizationBizTypeCode1 = this.selectedCommonCode.key;

      if (
        this.authorizationLineInfo.list.length === 0 ||
        // Object.keys(this.selectAuthorizationCode2).length == 0 ||
        this.authorizationLineInfo.authorizationBizTypeCode1 === "" ||
        this.authorizationLineInfo.authorizationBizTypeCode2 === "" ||
        this.authorizationLineInfo.businessCommonStatusCode === "" ||
        this.selectRole === "" ||
        this.selectBranchType === "" || 
        this.selectMandateoryApprovalYN === ""
      ) {
        this.isInvalid = true;
        return;
      }

      if (!this.isMandateoryNo("Y")) {
        this.isInvalid = true;
        return;
      }

      if (this.authorizationLineInfo.list.length !== 0) {
        this.newList = [];
        this.authorizationLineInfo.list.forEach((item, index) => {
          this.newList.push({
            branchTypeCode: item.branchTypeCode,
            mandateoryApprovalYN: item.mandateoryApprovalYN,
            authorizationRoleCode: item.authorizationRoleCode,
            seqNo: index + 1
          })
        })
      } else {
        this.newList.push({
          branchTypeCode: this.selectBranchType,
          mandateoryApprovalYN: this.selectMandateoryApprovalYN,
          authorizationRoleCode: this.selectRole,
          seqNo: 1
        });
      }

      this.$dialog.onOpen({
        content: `${this.$t('message.would_you_like_to_save')}`,
        dialogMode: 'Question',
        showCancel: true,
        callback: () => {
          if (this.queryRoute.type === "edit") {
            this.updateAuthorizationLine().then((res: boolean) => {
              if (res) {
                this.authorizationLineDetail();
                this.inputUpdate();
                modalController.dismiss();
              }
            });
          } else {
            this.createAuthorizationLine().then((res: boolean) => {
              if (res) {
                this.goToList();
              }
            });
          }
          modalController.dismiss();
        },
      });
    },

    async inquiryAuthorizationCode2() {
      const _oBody: APM20100113_BODY = {
        authorizationBizTypeCode1: this.authCode1,
      };

      const res = await this.$request.getRequest("APM20100113", _oBody, false) as APM20100113_RES;

      if (res.header.result) {
        this.listAuthorizationBizTypeCode2 = JSON.parse(JSON.stringify(res.body.list));
        // this.isInvalid === false

        // if (this.listAuthorizationBizTypeCode2.length != 0) {
        //   this.selectAuthorizationCode2 = this.listAuthorizationBizTypeCode2[0];
        // } else {
        //   this.authorizationLineInfo.authorizationBizTypeCode2 = "";
        //   this.selectAuthorizationCode2 = {} as AUTHORIZATION_CODE2_LIST;
        //   this.isInvalid === true
        //   return
        // }
      }
    },

    async authorizationLineDetail() {
      const _oBody: APM20100212_BODY = {
        authorizationAuthorityTypeCode: String(this.queryRoute.authorizationAuthorityTypeCode),
        authorizationBizTypeCode1: String(this.queryRoute.authorizationBizTypeCode1),
        authorizationBizTypeCode2: String(this.queryRoute.authorizationBizTypeCode2),
      }

      const res = await this.$request.getRequest("APM20100212", _oBody, false) as APM20100212_RES;

      if (res.header.result) {
        this.modifyCard = {
          registerTellerId: String(res.body.firstRegisterTellerID),
          registerTellerName: String(res.body.firstRegisterTellerName),
          registerDate: String(res.body.firstRegisterDate),
          registerTime: String(res.body.firstRegisterTime),
          updatedTellerId: String(res.body.lastChangeTellerID),
          updatedTellerName: String(res.body.lastChangeTellerID),
          updatedDate: String(res.body.lastChangeDate),
          updatedTime: String(res.body.lastChangeTime),
          deleteTellerId: '',
          deleteTellerName: '',
          deleteDate: '',
          deleteTime: '',
        };

        this.authorizationLineInfo = JSON.parse(JSON.stringify(res.body));

        this.authCode1 = res.body.authorizationBizTypeCode1;
        this.authCode2Edit.authorizationBizTypeCode2 = res.body.authorizationBizTypeCode2;
        this.authorizationLineInfo.authorizationBizTypeCode2 = res.body.authorizationBizTypeCode2;
        this.authCode2Edit.managerApprovalDescription = res.body.managerApprovalDescription;
        this.selectRole = this.authorizationLineInfo.list[0].authorizationRoleCode;
        this.selectedCommonCode.key = res.body.authorizationBizTypeCode1;
        this.selectedCommonCode.value = `${this.$codeConverter.convertValue('BusinessCategoryCode', res.body.authorizationBizTypeCode1)}`;
        this.inquiryAuthorizationCode2();
      }
    },

    async inquiryPortalRoleList() {
      const _oBody: COM20700111_BODY = {
        userRoleID: "",
        userRoleName: "",
        userRoleStatusCode: "",
        pageSize: 10000
      };

      const res = await this.$request.getRequest("COM20700111", _oBody, false) as COM20700111_RES;

      if (res.header.result) {
        this.listPortalRole = JSON.parse(JSON.stringify(res.body.list));
        this.queryRoute.type === "edit" ? this.selectRole = this.listPortalRole.length !== 0 ? this.listPortalRole[0].userRoleID : '' : '';
      }
    },

    async createAuthorizationLine() {
      const _oBody: APM20100221_BODY = {
        authorizationBizTypeCode1: this.authorizationLineInfo.authorizationBizTypeCode1,
        authorizationBizTypeCode2: this.authorizationLineInfo.authorizationBizTypeCode2,
        authorizationAuthorityTypeCode: this.authorizationLineInfo.authorizationBizTypeCode2,
        businessCommonStatusCode: this.authorizationLineInfo.businessCommonStatusCode,
        list: this.newList
      };

      const res = await this.$request.getRequest("APM20100221", _oBody, false) as APM20100221_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: 'Success',
          callback: () => {
            modalController.dismiss()
          }
        });
      }

      return res.header.result
    },

    async updateAuthorizationLine() {
      const _oBody: APM20100231_BODY = {
        authorizationBizTypeCode1: this.authorizationLineInfo.authorizationBizTypeCode1,
        authorizationBizTypeCode2: this.authorizationLineInfo.authorizationBizTypeCode2,
        authorizationAuthorityTypeCode: this.authorizationLineInfo.authorizationAuthorityTypeCode,
        businessCommonStatusCode: this.authorizationLineInfo.businessCommonStatusCode,
        list: this.newList
      };

      const res = await this.$request.getRequest("APM20100231", _oBody, false) as APM20100231_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: 'Success',
          callback: () => {
            this.authorizationLineDetail()
            this.inputUpdate()
            modalController.dismiss()
          }
        });
      }

      return res.header.result
    },

    isMandateoryNo(isMandateoryYN: string) {
      const isMandateoryNo = this.authorizationLineInfo.list.some(obj => Object.values(obj).includes(isMandateoryYN))
      return isMandateoryNo
    },

    goToList() {
      this.$router.push('APM201002SC00');
    },

    handleCancel() {
      this.$dialog.onOpen({
        content: `${this.$t('message.cancel_without_saving_changes')}`,
        dialogMode: 'Question',
        callback: () => {
          if (this.queryRoute.type === "edit") {
            this.inputUpdate();
            this.authorizationLineDetail();
          } else {
            this.goToList();
          }
          modalController.dismiss();
        },
      });
    },

    dialogValidate(content: string) {
      this.$dialog.onOpen({
        content: content,
        dialogMode: "Question",
        callback: () => modalController.dismiss()
      });
    },

    dialogMessage(message: string) {
      this.$dialog.onOpen({
        content: message,
        dialogMode: "Warning",
        callback: () => {
          modalController.dismiss()
        }
      });
    },
  },
});
